<template>
  <div>
    <banner></banner>
    <formulario></formulario>
    <contactos></contactos>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./contactos/Banner";
import Form from "./contactos/Formulario";
import Contactos from "./contactos/Contactos";
// import FadeIn from "../animations/fade";

export default {
  // data() {
  //   return {
  //     FadeIn,
  //   };
  // },
  components: {
    banner: Banner,
    formulario: Form,
    contactos: Contactos,
  },
  metaInfo() {
    return {
      title: "Contactos - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content:
            "Visite a nossa clínica ou entre em contacto connosco através do nosso número de telefone ou do nosso formulário online.",
        },
        {
          property: "og:title",
          content: "Contactos - Ana Carolina Pereira - Clínica de Psicologia",
        },
        {
          property: "og:url",
          content: "https://www.anacarolinapereira.pt/contactos",
        },
        {
          property: "og:description",
          content:
            "Visite a nossa clínica ou entre em contacto connosco através do nosso número de telefone ou do nosso formulário online.",
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
</style>



